@font-face {
  font-family: "robotoBold";
  src: url("../../fonts/Roboto-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
.hero__conteiner--img {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.hero {
  position: relative;
  max-width: 90rem;
  height: 100vh;
  overflow: hidden;
}
.hero__conteiner {
  display: grid;
  justify-items: stretch;
  padding-left: 14.3rem;
  padding-right: 14.3rem;
  gap: 6rem;
  grid-template-columns: 1fr 1fr;
  height: 100%;
}
.hero__conteiner--left {
  padding-top: 6rem;
}
.hero__conteiner--img {
  height: 34.375vh;
  width: 35.069vw;
  background-image: url("../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_ar_4_3,c_fill,g_auto__c_scale,w_538.jpg");
}
.hero__conteiner--right {
  padding-top: 8.5rem;
}
.hero__conteiner--title {
  height: 5vh;
  margin-bottom: 1.5rem;
}
.hero__conteiner--title h1 {
  font-size: 3rem;
  line-height: 3.2rem;
}
.hero__conteiner--paragraph {
  height: 6rem;
  width: 26.389vw;
  margin-bottom: 2rem;
}
.hero__conteiner--paragraph p {
  line-height: 1.5rem;
  font-size: 1rem;
}
.hero__conteiner--btn {
  cursor: pointer;
  padding: 1rem 2.5rem;
  border-radius: 32px;
  background-color: #E4C1AE;
  border: none;
  color: #FFFFFF;
}

@font-face {
  font-family: "robotoBold";
  src: url("../../fonts/Roboto-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@media (min-width: 320px) and (max-width: 480px) {
  .hero__conteiner {
    padding-left: 0%;
    padding-right: 0%;
    grid-template-columns: 1fr;
    height: 100%;
  }
  .hero__conteiner--left {
    padding-top: 0%;
  }
  .hero__conteiner--img {
    background-image: image-set(url("../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_480.jpg"));
    width: 100vw;
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .hero__conteiner--right {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding-top: 0%;
    height: 100vh;
    width: 100vw;
    text-align: center;
  }
  .hero__conteiner--title {
    width: 100vw;
  }
  .hero__conteiner--title h1 {
    font-size: 4rem;
  }
  .hero__conteiner--paragraph {
    height: auto;
    width: 100vw;
  }
  .hero__conteiner--paragraph p {
    font-size: 2rem;
    line-height: 2.3rem;
  }
  .hero__conteiner--btn {
    font-size: 2rem;
    background-color: rgb(73, 59, 59);
  }
}

@media (min-width: 480px) and (max-width: 769px) {
  .hero__conteiner {
    padding-left: 0%;
    padding-right: 0%;
    grid-template-columns: 1fr;
    grid-template-rows: 40% 1fr;
    gap: 0;
    height: 100%;
  }
  .hero__conteiner--left, .hero__conteiner--right {
    padding-top: 0%;
  }
  .hero__conteiner--img {
    width: 100vw;
    background-image: image-set(url("../../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_784.jpg"));
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 95%;
  }
  .hero__conteiner--right {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
  }
  .hero__conteiner--title {
    order: 2;
  }
  .hero__conteiner--title h1 {
    font-size: 4.3rem;
  }
  .hero__conteiner--paragraph {
    text-align: center;
    width: 98vw;
    height: auto;
    order: 3;
  }
  .hero__conteiner--paragraph p {
    font-size: 1.7rem;
    line-height: 2rem;
  }
  .hero__conteiner--btn {
    order: 1;
    width: 70vw;
    font-size: 2.2rem;
    margin-top: -10%;
  }
}

* {
  font-size: 16px;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "robotoBold", sans-serif;
  font-style: normal;
}